import { FC, useEffect, useRef, useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';

import { setSortType, selectSort } from '../redux/slices/filter/filterSlice';

type SortProperty = 'rating' | '-rating' | 'price' | '-price' | 'title' | '-title';

interface ISortType {
  name: string;
  sort: SortProperty;
}

export const list: ISortType[] = [
  { name: ' (ASC)', sort: '-rating' },
  { name: ' (DESC)', sort: 'rating' },
  { name: ' (ASC)', sort: '-price' },
  { name: ' (DESC)', sort: 'price' },
  { name: ' (ASC)', sort: '-title' },
  { name: ' (DESC)', sort: 'title' },
];

const Sort: FC = () => {
  const dispatch = useDispatch();
  const { sortType } = useSelector(selectSort);
  const sortRef = useRef<HTMLDivElement>(null);

  const [isVisible, setIsVisivle] = useState(false);

  const onClickListItem = (obj: ISortType) => {
    dispatch(setSortType(obj));
    setIsVisivle(false);
  };

  useEffect(() => {
    const handleClickOutside = (event: MouseEvent) => {
      if (sortRef.current && !event.composedPath().includes(sortRef.current)) {
        setIsVisivle(false);
      }
    };

    document.body.addEventListener('click', handleClickOutside);

    return () => document.body.removeEventListener('click', handleClickOutside);
  }, [isVisible]);

  return (
    <div ref={sortRef} className="sort">
      <div className="sort__label">
        <svg
          width="10"
          height="6"
          viewBox="0 0 10 6"
          fill="none"
          xmlns="http://www.w3.org/2000/svg">
          <path
            d="M10 5C10 5.16927 9.93815 5.31576 9.81445 5.43945C9.69075 5.56315 9.54427 5.625 9.375 5.625H0.625C0.455729 5.625 0.309245 5.56315 0.185547 5.43945C0.061849 5.31576 0 5.16927 0 5C0 4.83073 0.061849 4.68424 0.185547 4.56055L4.56055 0.185547C4.68424 0.061849 4.83073 0 5 0C5.16927 0 5.31576 0.061849 5.43945 0.185547L9.81445 4.56055C9.93815 4.68424 10 4.83073 10 5Z"
            fill="#2C2C2C"
          />
        </svg>
        <b> :</b>
        <span onClick={() => setIsVisivle(!isVisible)}>{sortType.name}</span>
      </div>
      {isVisible && (
        <div className="sort__popup">
          <ul>
            {list.map((item, i) => (
              <li
                key={i}
                onClick={() => onClickListItem(item)}
                className={item.sort === sortType.sort ? 'active' : ''}>
                {item.name}
              </li>
            ))}
          </ul>
        </div>
      )}
    </div>
  );
};

export default Sort;
